<template>
  <div name="about">
    <CoverImage page="About" backgroundImage="backgnd_2.jpg" title="About Us" />

    <v-container>
      <v-layout row wrap class="ma-2">
        <!-- Gaurav Deshpande -->
        <v-flex sm12 md6>
          <v-img :src="require('@/assets/images/Gaurav_Deshpande.png')" alt="Gaurav Deshpande" />
        </v-flex>

        <!-- About us -->
        <v-flex sm12 md6 class="pl-4">
          <v-tabs v-model="tabModel" grow centered>
            <v-tab v-for="(tab, i) in tabs" :key="i" :href="`#tab-${i}`">{{ tab.heading }}</v-tab>
            <v-tab-item v-for="(tab, i) in tabs" :key="i" :value="`tab-${i}`">
              <v-card flat tile>
                <v-card-text v-if="tab.text.constructor.name=='Array'">
                  <p v-for="(text,i) in tab.text" :key="i">{{ text }}</p>
                </v-card-text>
                <v-card-text v-else>{{tab.text}}</v-card-text>

                <v-card-text v-if="i==0">
                  <v-list flat tile class="ml-n4 mt-n10">
                    <v-list-item>
                      <v-list-item-icon>
                        <v-img
                          src="/static/images/tfv-logo.png"
                          width="40"
                          alt="Travelfreak Vacations"
                        ></v-img>
                      </v-list-item-icon>
                      <v-list-item-content class="ml-n4">
                        <a href="mailto:info@travelfreakvacations.com">info@travelfreakvacations.com</a>
                        +91 9004052169
                      </v-list-item-content>
                    </v-list-item>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-tab-item>
          </v-tabs>
        </v-flex>
      </v-layout>
    </v-container>
    <br />

    <!-- Frequently asked Questions -->
    <v-container fluid class="light-blue lighten-5 pa-4">
      <p class="display-0 text--disabled">FAQs</p>
      <h2>
        <strong>Frequently</strong>
        <font class="font-weight-light">&nbsp; Asked Questions</font>
      </h2>

      <v-layout row wrap justify-space-around class="ma-2 mp-2">
        <v-flex sm10 md5>
          <v-expansion-panels tile multiple focusable>
            <v-expansion-panel v-for="i in faqsHalfLength" :key="i">
              <v-expansion-panel-header class="display-0 font-weight-bold">
                {{faqs[i-1].ques}}
                <template v-slot:actions>
                  <v-icon color="warning">mdi-arrow-down-drop-circle-outline</v-icon>
                </template>
              </v-expansion-panel-header>
              <v-expansion-panel-content class="mt-2">{{faqs[i-1].ans}}</v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-flex>
        <v-flex sm10 md5>
          <v-expansion-panels tile multiple focusable>
            <v-expansion-panel v-for="i in faqsHalfStartIndex" :key="i">
              <v-expansion-panel-header class="display-0 font-weight-bold">
                {{faqs[faqsHalfStartIndex+i-1].ques}}
                <template v-slot:actions>
                  <v-icon color="warning">mdi-arrow-down-drop-circle-outline</v-icon>
                </template>
              </v-expansion-panel-header>
              <v-expansion-panel-content class="mt-2">{{faqs[faqsHalfStartIndex+i-1].ans}}</v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-flex>
      </v-layout>
      <br />
    </v-container>
  </div>
</template>

<script>
import CoverImage from "@/components/common/CoverImage.vue";

export default {
  name: "About",
  components: {
    CoverImage
  },
  data: () => {
    return {
      tabModel: null,
      tabs: [
        {
          heading: "Who are we?",
          text: [
            "Gaurav Deshpande, the owner and Founder of Travelfreak Vacations has travelled to \
							over 25 countries and explored them on his own. Also, a Wildlife and Travel Photographer, he has mastered the destinations that will help you to see some exotic offbeat locations that are just known to the locals. He has always \
              believed that exploring and enjoying any place best depends on your likes, speed and time.",
            "This belief gave birth to Travelfreak Vacations - customized holidays that allow you to see and \
              pay only for what you want, and backpacker tours that make you feel like a local.",
            "Gaurav is a software engineer as well as an IATA consultant in Travel & Tourism (Canada), \
              so you get the best crafted itinerary with right mix of travel & technology. He has received affiliations \
              as a destination expert from more than 10 countries, which makes us “Travelfreak Vacations” stand out from the rest. \
              He has contributed to many everlasting memories in the lives of his customers and friends alike by \
              crafting and arranging unique & exiting travel itineraries to best meet their interests and \
              budget. We look forward to having you next!"
          ]
        },
        {
          heading: "Our mission",
          text:
            "To add offbeat, unique and extraordinary destinations for all passionate travelers! To make travel possible for everyone by introducing the world to them!"
        },
        {
          heading: "Our goal",
          text:
            "To give people maximum feel of their chosen destination. To give them the opportunity to make the most of their holiday time. To make people happy and help them explore the unexplored is our utmost goal."
        }
      ],
      faqs: [
        {
          ques: "Which destinations do you offer?",
          ans:
            "We have a network across 7 continents. You name a destination of your choice and we will do everything for you."
        },
        {
          ques: "What size groups do you customize for?",
          ans: "From solo person to 15 travellers."
        },
        {
          ques:
            "In how much advance time do we need to get the tour customized?",
          ans:
            "It's always better to plan ahead of time, but thanks to our network, your customized package can be done up to 2 days before departure."
        },
        {
          ques: "Do you provide escorted tours?",
          ans: "Yes, we do provide escorted tours."
        }
      ]
    };
  },
  computed: {
    faqsHalfLength: function() {
      return this.faqs.length / 2;
    },
    faqsHalfStartIndex: function() {
      return this.faqs.length - this.faqs.length / 2;
    }
  }
};
</script>

<style scoped>
.rounded-card {
  border-radius: 50px;
}
.v-expansion-panels {
  z-index: 0;
}
</style>


